import React, { useState, useEffect } from "react"
import PropTypes from "prop-types"
import InfiniteScroll from "react-infinite-scroll-component"

import Carousel, { Modal, ModalGateway } from 'react-images';
import carouselFormatters from '../utils/carouselFormatters';
import './gallery.css'
interface Props {
    images: {
        id: string;
        aspectRatio: number;
        src: string;
        srcSet: string;
        sizes: string;
        originalImg: string;
        caption: string;
    }[];
}

const ImageGallery = ({ images,
    loading, fetchImages, setModalCurrentIndex, setModalIsOpen
}: Props) => {
    const openModal = (imageIndex) => {
        setModalCurrentIndex(imageIndex);
        setModalIsOpen(true);
    };

    if (!images) return <p>no images found</p>
    return (
        <InfiniteScroll
            dataLength={images.length}
            next={() => fetchImages(images)}
            hasMore={true}
            loader={
                <p style={{ textAlign: "center", marginTop: "1%" }}>
                    Scroll for more
        </p>
            }
        >
            <div className="image-grid">
                {!loading
                    ? images.map((image, index) => (
                        <div className="image-item" key={image.id}>
                            <img src={image.src}

                                onClick={() => openModal(index)} alt={image.caption} />
                        </div>
                    ))
                    : ""}
            </div>
        </InfiniteScroll>
    )
}

const InfiniteImages = ({ images }) => {
    // Hold state
    const [imageState, setImages] = useState([])
    const [loading, setLoading] = useState(true)
    const [modalIsOpen, setModalIsOpen] = useState(false);
    const [modalCurrentIndex, setModalCurrentIndex] = useState(0);

    const closeModal = () => setModalIsOpen(false);
    // Fetch images on component mount
    useEffect(() => {
        fetchImages()
    }, [])


    // Fetch Images from functions
    const fetchImages = () => {
        console.log('fetch images')
        setImages(images)
        setLoading(false)
    }
    return (<div>
        <ImageGallery images={images}
            setModalCurrentIndex={setModalCurrentIndex}
            setModalIsOpen={setModalIsOpen}
            loading={loading} fetchImages={fetchImages} />
        {ModalGateway && (
            <ModalGateway>
                {modalIsOpen && (
                    <Modal onClose={closeModal}>
                        <Carousel
                            views={images.map(({ src, caption }) => ({
                                source: src,
                                caption: caption
                            }))}
                            currentIndex={modalCurrentIndex}
                            formatters={carouselFormatters}
                            components={{ FooterCount: () => null }}
                        />
                    </Modal>
                )}
            </ModalGateway>
        )}
    </div>
    )
}

ImageGallery.propTypes = {
    images: PropTypes.array,
    loading: PropTypes.bool,
    fetchImages: PropTypes.func,
}

export default InfiniteImages
